<template>
  <form @submit.prevent="submit">
    <div class="d-flex justify-content-center">
      <div class="row">
        <div class="col">
          <div class="edit-video">
            <label for="exampleInputEmail1" class="form-label">title</label>
            <input type="text" class="form-control" v-model="title" />

            <label for="exampleInputEmail1" class="form-label"
              >Description</label
            >
            <input type="text" class="form-control" v-model="Description" />

            <label for="exampleInputEmail1" class="form-label">HD</label>
            <input type="text" class="form-control" v-model="HD" />

            <label for="exampleInputEmail1" class="form-label">SD</label>
            <input type="text" class="form-control" v-model="SD" />

            <button type="submit" class="btn btn-info" @click="UpdateVideo">
              Edit
            </button>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import VideosServices from "@/services/VideosServices";

export default {
  name: "edit-video",

  data() {
    return {
      video: {
        title: null,
        Description: null,
        HD: null,
        SD: null,
      },
    };
  },

  created() {
    this.EditVideo();
  },
  methods: {
    async EditVideo() {
      try {
        const video_Id = this.$route.params.video_Id;
        this.video = await (await VideosServices.show(video_Id)).data;
        console.log(this.video);
      } catch (error) {
        this.error = error.response.data.error;
      }
    },
    async UpdateVideo() {
      const video_Id = this.$route.params.video_Id;
      await VideosServices.update(video_Id);
    },
  },
};
</script>

