import Api from "./Api";

export default {
  video(Video) {
    return Api().post("api/v1/videoCreate", Video);
  },

  index(Video) {
    return Api().get("api/v1/videos", Video);
  },

  show(video_Id) {
    return Api().get("api/v1/video/" + video_Id);
  },

  delete(video_Id) {
    return Api().delete("api/v1/videodelete/" + video_Id);
  },

  update(video_Id) {
    return Api().put("api/v1/videoupdate/" + video_Id);
  },
};
