<template>
  <SideBar />
  <div class="Movies-List mt-5">
    <table class="table List">
      <thead>
        <tr>
          <th scope="col">Thumbnail</th>
          <th scope="col">Title</th>
          <th scope="col">Fullhd</th>
          <th scope="col">HD</th>
          <th scope="col">SD</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="video in videos" :key="video._id">
          <th scope="row ">
            <img class="thumbnail" src="@/assets/Raya.jpg" alt="" srcset="" />
            <!-- <img class="thumbnail" :src="video.receipt" /> -->
          </th>
          <th>{{ video.title }}</th>
          <td>{{ video.Description }}</td>
          <td>{{ video.HD }}</td>
          <td>{{ video.SD }}</td>
          <td class="button-video">
            <router-link
              class="btn btn-info"
              :to="{ name: 'edit-video', params: { video_Id: video._id } }"
              >Edit</router-link
            >

            <button
              type="button"
              class="btn btn-danger"
              @click="deleteMovie(video._id)"
            >
              Delete
            </button>

            <router-link
              class="btn btn-danger"
              :to="{ name: 'video-view', params: { video_Id: video._id } }"
              >View
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import SideBar from "@/components/Sidebar";
import VideoService from "@/services/VideosServices";

export default {
  name: "MoviesList",

  data() {
    return {
      videos: [],
    };
  },

  created() {
    this.getMovies();
  },

  methods: {
    async getMovies() {
      try {
        const response = await VideoService.index();
        this.videos = response.data;
        console.log(response.data);
      } catch (error) {
        console.log(error);
      }
    },

    async deleteMovie(video_Id) {
      try {
        await VideoService.delete(video_Id);
        this.getMovies();
      } catch (error) {
        console.log(error);
      }
    },
  },

  components: {
    SideBar,
  },
};
</script>

