<template>
  <form @submit.prevent="submit">
    <font-awesome-icon icon="fa-solid fa-address-book" />
    <div class="d-flex justify-content-center">
      <div class="row">
        <div class="col">
          <div
            class="alert alert-danger mt-5 mb-2"
            v-if="message"
            role="alert"
            v-html="message"
          ></div>

          <div class="card mt-5 pb-5 px-5" style="width: 30rem">
            <h1 class="mt-2">Register</h1>
            <input
              class="mt-2 form-control p-3"
              type="text"
              name="name"
              placeholder="Name"
              v-model="name"
            />
            <input
              class="mt-2 form-control p-3"
              type="email"
              name="email"
              placeholder="Email"
              v-model="email"
            />
            <input
              class="mt-2 form-control p-3"
              type="password"
              name="password"
              placeholder="Password"
              v-model="password"
            />
            <!-- <input
              class="mt-2"
              type="repeat_password"
              name="repeat_password"
              placeholder="Repeat Password"
              v-model="repeat_password"
            /> -->
            <button
              class="btn btn-dark mt-4 p-3"
              type="submit"
              @click="register"
            >
              Register
            </button>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import AuthenticationService from "../services/Authentication";
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "register",
  data() {
    return {
      name: "",
      email: "",
      password: "",
      error: null,
      message: null,
    };
  },

  methods: {
    async register() {
      try {
        const response = await AuthenticationService.register({
          name: this.name,
          email: this.email,
          password: this.password,
        });
        this.$store.dispatch("SET_TOKEN", response.data.token);
        this.$store.dispatch("SET_USER", response.data.user);
        // this.$router.push({
        //   path: "/login",
        // });
        this.$toast.success(`Hey! I'm here`);
        this.message = response.data.message;
      } catch (error) {
        this.error = error.response.data.error;
        this.$toast.error("هذا البريد موجود بالفعل");
      }
    },
  },
};
</script>

<style scoped>
i {
  font-size: 2rem;
  color: #000;
}
</style>
