<template>
  <form @submit.prevent="submit">
    <div class="d-flex justify-content-center">
      <div class="row">
        <div class="col">
          <div
            class="alert alert-danger mt-5 mb-2"
            v-if="error"
            role="alert"
            v-html="error"
          ></div>

          <div class="card mt-5 pb-5 px-5" style="width: 30rem">
            <h1 class="mt-2">Login</h1>
            <input
              class="mt-2 form-control p-3"
              type="email"
              name="email"
              placeholder="Email"
              v-model="email"
            />
            <input
              class="mt-2 form-control p-3"
              type="password"
              name="password"
              placeholder="Password"
              v-model="password"
            />
            <button class="btn btn-dark mt-4 p-3" type="submit" @click="Login">
              login
            </button>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import AuthenticationService from "../services/Authentication";

export default {
  data() {
    return {
      email: "",
      password: "",
      error: "",
      isLoading: false,
    };
  },

  methods: {
    async Login() {
      try {
        const response = await AuthenticationService.Login({
          email: this.email,
          password: this.password,
        });
        this.$store.dispatch("SET_TOKEN", response.data.token);
        this.$store.dispatch("SET_USER", response.data.user);
        // this.$router.push({
        //   path: "/profile",
        // });
        localStorage.setItem("token", response.data.token);
      } catch (error) {
        this.error = error.response.data.error;
      }
    },
  },
};
</script>

<style scoped></style>
