<template>
  <SideBar />
  <HeaderDashboard />
  <main>
    <!-- <div class="col-9"> -->
    <div class="cards">
      <div class="upload">
        <div class="alert alert-success" v-if="success">
          Success
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="alert"
            aria-label="Close"
          ></button>
        </div>
        <form @submit.prevent="submit" enctype="multipart/form-data">
          <div class="video-title"></div>
          <div class="video-qwlte">
            <div class="mb-3">
              <label for="exampleInputEmail1" class="form-label">File</label>
              <input type="file" class="form-control" name="receipt" />
            </div>
            <div class="mb-3">
              <label for="exampleInputPassword1" class="form-label"
                >title</label
              >
              <input
                type="text"
                class="form-control"
                id="exampleInputPassword1"
                v-model="title"
              />
              <label for="exampleInputPassword1" class="form-label"
                >Description</label
              >
              <input type="text" class="form-control" v-model="Description" />

              <label for="exampleInputPassword1" class="form-label">HD</label>
              <input type="text" class="form-control" v-model="HD" />

              <label for="exampleInputPassword1" class="form-label">SD</label>
              <input type="text" class="form-control" v-model="SD" />
            </div>
          </div>
        </form>
        <div class="error">{{ error }}</div>
        <button class="mt-4 p-3 btn btn-dark" type="submit" @click="video">
          send
        </button>
      </div>
    </div>
  </main>
</template>

<script>
import VideoService from "@/services/VideosServices";
import SideBar from "@/components/Sidebar";
import HeaderDashboard from "@/components/HeaderDashoaerd.vue";

export default {
  name: "UploadFiles",

  components: {
    SideBar,
    HeaderDashboard,
  },

  data() {
    return {
      title: null,
      Description: null,
      HD: null,
      SD: null,
      receipt: null,
      error: null,
      success: false,
    };
  },
  methods: {
    successs() {
      return this.$store.getters.get_success;
    },
    onFileSelected(event) {
      this.receipt = event.target.files[0];
      console.log(this.receipt);
    },
    async video() {
      const fd = new FormData();
      fd.append("image", this.receipt);
      try {
        const response = await VideoService.video({
          title: this.title,
          Description: this.Description,
          HD: this.HD,
          SD: this.SD,
          success: this.success,
        });
        console.log(response.data);
      } catch (error) {
        this.error = error.response.data.error;
      }
    },
  },
};
</script>

