import Api from "./Api";

export default {
  register(credentials) {
    return Api().post("/api/v1/register", credentials);
  },

  Profile(credentials) {
    return Api().get("api/v1/me", credentials);
  },

  Login(credentials) {
    return Api().post("api/v1/auth", credentials);
  },

  VerfiyEmail(userId, token) {
    return Api().post("api/v1/:id/verify/:token/" + userId + token);
  },
};
